@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #fff;
  --primary-pink: rgb(241, 129, 148);
  --primary-dark: rgba(255,255,255, 0);
  --primary-red:#ef4035;
  --primary-hover-red:#f33501;
  --primary-grey:#ccd1d9;
  --primary-dark-grey:#666666;
  --primary-black:#000000;
  --primary-transparent-black: rgba(0,0,0,0.7);
  --primary-green: green;

}

body {
  font-family: 'Roboto Condensed', sans-serif !important;
}


/************************ NAVBAR *********************************/
nav{
  z-index: 2;
}
.logo {

  width: 11.25rem;
}

/*----- MENU LINKS --------*/
nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgba(255,255,255, 0);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}
@media(max-width:768px){
  .navbar-brand{
    width: 5rem;
  }
  }
.navbar-nav > li > a.active,  .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid rgb(241, 129, 148);
  border-top: 0.1875rem solid var(--primary-pink);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid rgb(241, 129, 148);
  border-top: 0.1875rem solid var(--primary-pink);
}

/************************ Header *********************************/
.header-wrapper{
  position:relative;
  background: url(/Portfolio_Project/static/media/wall-and-laptop-background.f9e274f4.jpg) no-repeat;
  background-size: cover;
  background-position:center;
  height: 100vh;
  
}
.main-info{
  display:flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

z-index: 1;
}

.main-info h1 {
  color: rgb(241, 129, 148);
  color: var(--primary-pink);
  text-transform: uppercase;
  
}
@media(max-width:768px){
  .main-info h1{
    font-size: 1.5rem !important;
  }
.typed-text{
  font-size: 1.3rem !important;
}
}
/************************ Typed Text *********************************/
.typed-text{
  font-size: 2rem;
  color:#fff;
  color:var(--primary-white);

}
/************************ Button Main Offer *********************************/

.btn-main-offer{
border: 1px solid #ef4035;
border: 1px solid var(--primary-red);
text-transform: uppercase;
border-radius: 0;
padding: 0.625px 0.75px 0.75px 0.75px;
color: #fff;
color: var(--primary-white);
margin-top: 2rem;
}
.btn-main-offer:hover{
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  transition: .2s ease-in-out;
}
/************************Particles *********************************/

.tsparticles-canvas-el{
position: absolute;
height: 100vh;
width: 100vw;
z-index: 1;
}

@media(max-width:768px){
  .particles-canvas{
    display: none;

  }
}
/************************ About Me*********************************/
.photo-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile-img{
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border:1px solid rgb(241, 129, 148);
  border:1px solid var(--primary-pink)
}
.about-heading{
  color: rgb(241, 129, 148);
  color: var(--primary-pink);
  text-transform: uppercase;
}
@media(max-width: 468){
  .profile-img{
  width: 10rem;
  }
}

/************************ Experience*********************************/
.experience{
  padding: 0.7rem;
}
.experience-wrapper{
  position: relative;
  padding: 3.125rem 0;
  overflow:hidden;
}
.experience h1{
  text-transform: uppercase;
  color: rgb(241, 129, 148);
  color: var(--primary-pink);

}
.experience-wrapper::before{
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width:0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;

}
.timeline-block{
  width:calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear:both;
}
.timeline-block-right{
  float: right;

}
.timeline-block-left{
  float: left;
  direction:rtl;
}
.marker{
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border:0.3125rem solid rgb(54, 54, 54);
  background: rgb(241, 129, 148);
  background: var(--primary-pink);
  margin-top: 0.625rem;
  z-index: 3;
}
.timeline-content{
  width:95%;
  padding: 0 0.9375rem;
  color: #ccd1d9;
  color: var(--primary-grey);
  direction: ltr;
}
.timeline-content h3{
  margin-top: 0.3125rem;
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
}
/******** Media ********/
@media(max-width:768px){
  .experience .container{
    padding-left: 0;
  }
  .experience-wrapper::before{
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right{
    float: none;
    width: 100%;

  }
  .timeline-block-left{
    float: none;
    direction: ltr;
    width: 100%;
  }
}

h3 {
  text-align: center;
  margin-top: 1rem;
  color:rgb(241, 129, 148);
  color:var(--primary-pink) 
}
form {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

 .download{
  margin: 1rem 0 0 0;
  
  background: rgb(241, 129, 148);
  
  background: var(--primary-pink);}
/******** Portfolio ********/

.portfolio-wrapper{
  background: #ccd1d9;
  background: var(--primary-grey);
  padding: 3rem 0;
}
.portfolio-wrapper h1{
  color:rgb(241, 129, 148);
  color:var(--primary-pink)
}
.portfolio-image{
  width: 15rem;
  height: 8rem;
  border: 1px solid rgb(241, 129, 148);
  border: 1px solid var(--primary-pink);
  padding: 0 0.5rem;
}
.portfolio-image-box{
  position: relative;
  margin: 1rem;
}

/******** Overflow ********/
.overflow{
  position: absolute;
  opacity: 0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}
.portfolio-image-box:hover .overflow{
  opacity: 60%;
  transition: 0.3s ease-in-out;
}
.portfolio-icon{
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: rgb(241, 129, 148);
  color: var(--primary-pink);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon{
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}
.popupbox-wrapper{

margin-top: 7rem;}
/******** Overflow ********/
.portfolio-image-popupbox{
  width: 45rem;
  padding: 0 0.5rem;
}
.hyper-link{
  cursor: pointer;
  color: rgb(241, 129, 148);
  color: var(--primary-pink);
}
.hyper-link:hover{
    color: #ef4035;
    color: var(--primary-red);
}
/******** Media********/
@media(max-width: 768px){
  .portfolio-image-popupbox{
    width:100
  }
}
@media(max-height: 570px){
  .popupbox-wrapper{
    height: 100%;
  }
  .portfolio-image-popupbox{
    width:60%
  }
}
@media(max-height: 280px){
  .popupbox-wrapper{
    height: 90%;
    
  }
  .portfolio-image-popupbox{
    width:30%
  }
}

/************************ CONTACTS COMPONENT *********************************/
.contacts {
  background: rgba(255,255,255, 0);
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: rgb(241, 129, 148);
  color: var(--primary-pink);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

/*------------------ INPUTS -------------------------------*/
.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: rgb(241, 129, 148);
  color: var(--primary-pink);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: rgb(241, 129, 148);
  background: var(--primary-pink);
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: rgb(241, 129, 148);
  background: var(--primary-pink);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: #fff;
  color: var(--primary-white);
  background: #f33501;
  background: var(--primary-hover-red);
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: green;
  color: var(--primary-green);
}

/*---------------- MEDIA -----------------------*/
@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  
  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}


/************************ FOOTER COMPONENT *********************************/
.footer {
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: var(--primary-orange);
  cursor:pointer;
}

.footer p {
  margin-bottom: 0;
}



































